.portfolio-page {

  &::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    filter: brightness(52.5%);
    background-image: url(../img/works-bg.jpg);
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
  }

  &__content {
  }
  
  &__works {
    padding: 15.5rem 20rem 5rem;
    @include to(1742px) {
      grid-template-columns: minmax(20rem, 50rem) minmax(20rem, 50rem);
      justify-content: center;
      padding: 10rem 15rem 5rem;
    }

    @include to(1130px) {
      grid-template-columns: minmax(20rem, 50rem);
      justify-content: center;
      padding: 15rem 2rem 5rem;
    }

    @include to(800px) {
      grid-template-columns: 1fr;
      padding-right: 12rem;
      padding-left: 12rem;
    }

    @include to(690px) {
      padding-right: 8rem;
      padding-left: 8rem;
    }

    @include to(576px) {
      padding-right: 2rem;
      padding-left: 2rem;
    }
    @include sm(padding-top, 8rem);

    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 3rem 2.5rem;
  }

  &__heading {
    @include to(1742px) {
      grid-row: 1 / 2;
      grid-column: 1 / 3;
    }

    @include to(1130px) {
      grid-row: 1 / 2;
      grid-column: 1 / 2;
    }

    @include sm(padding-bottom, 1rem);

    grid-row: 1 / 2;
    grid-column: 1 / 4;
  }
}