.navigation {
  @include sm(right, 2rem);
  @include sm(top, 2rem);
  @include to(576px) {
    right: 2rem;
  }
  position: fixed;
  right: 5.7rem;
  top: 5rem;
  z-index: 500;
  display: inline-block;
  &__checkbox,
  &__check-list {
      display: none;
  }

  &__button {
    @include sm(height, 2.2rem);
    display: inline-flex;
    height: 2rem;
    z-index: 2000;
    cursor: pointer;
    position: relative;
  }

  &__text {
    @include sm(display, none);
    font-size: 1.6rem;
    text-transform: uppercase;
    margin-right: .75rem;
  }

  &__nav {
    @include sm(width, 100vw);
    display: flex;
    position: fixed;
    z-index: 500;
    width: 30rem;
    height: 100vh;
    right: 0;
    top: 0;
    background-image: linear-gradient(270deg, rgba(0,0,0,1) 10%, transparent 100%);
    transform: translateX(100%);
    transition: all .5s;
  }

  &__content {
    margin: auto 0 auto auto;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
  }

  &__link {
    color: $text-color;
    font-weight: 600;
    font-size: 2.2rem;
    text-align: right;
    cursor: pointer;
    display: block;
    padding: 1.25rem 0;
    margin-right: 5.7rem;

    &:not(:last-child) {
      margin-bottom: 1.1rem;
    }

    &:link,
    &:visited {
        color: $color-white;
    }
  }

  &--active-index &__link:nth-child(1),
  &--active-portfolio &__link:nth-child(2),
  &--active-price &__link:nth-child(3),
  &--active-blog &__link:nth-child(4),
  &--active-contacts &__link:nth-child(5) {
    position: relative;
    font-style: italic;
    &::before {
      content: '';
      height: 2px;
      background-color: $color-white;
      width: 115%;
      position: absolute;
      right: -7.5%;
      top: 56%;
    }
  }


  //FUNCTIONALITY

  &__checkbox:checked ~ &__nav {
    transform: translateX(0);
  }


  //ICON
  &__icon {
    @include sm(flex, 0 0 2.5rem);
    position: relative;
    margin-top: 1rem;
    flex: 0 0 2rem;

    &,
    &::before,
    &::after {
      @include sm(width, 2.5rem);
      @include sm(height, 2px);
      width: 2rem;
      height: 1px;
      background-color: $color-white;
      display: inline-block;
      transition: all .2s;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
    }

    &::before {
      @include sm(top, -.7rem);
      top: -.5rem;
    }
    &::after {
      @include sm(top, .7rem);
      top: .5rem;
    }
  }

  &__button:hover &__icon::before {
    @include sm(top, -.8rem);
    top: -.6rem;
  }

  &__button:hover &__icon::after {
    @include sm(top, .8rem);
    top: .6rem;
  }

  &__checkbox:checked + &__button &__icon {
    background-color: transparent;
  }

  &__checkbox:checked + &__button &__icon::before {
    top: 0;
    transform: rotate(135deg);
  }

  &__checkbox:checked + &__button &__icon::after {
    top: 0;
    transform: rotate(-135deg);
  }
  &__checkbox:checked + &__button &__icon::after,
  &__checkbox:checked + &__button &__icon::before,
  &__checkbox:checked + &__button &__icon {
    @include sm(height, 3px);
    border-radius: 3px;
    height: 2px;
  }

  &--right {
    @include sm(right, 2rem);
    position: absolute;
    right: 16%;
    top: 5rem;
  }

  &--portfolio {
    @include md(position, absolute);
    @include to(800px) {
      right: 12rem;
    }
    @include to(690px) {
      right: 8rem;
    }
  }
}