.portfolio-item {
  display: inline-flex;
  flex-direction: column;
  height: 30rem;
  @include sm(height, 35rem);
  position: relative;
  box-shadow:0 0 20px rgba($color-white,.0);
  transition: all .3s;

  &:hover {
    box-shadow:0 0 20px rgba($color-white,.35);
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center center;
    z-index: 5;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 6;
    background-image: linear-gradient(180deg, transparent 0%, $color-black 90%);
  }

  &--sochi::after {
    background-image: url(../img/sochi.jpg);
  }
  &--plumbing::after {
    background-image: url(../img/plumbing.jpg);
  }
  &--mount::after {
    background-image: url(../img/explore.jpg);
  }
  &--sveter::after {
    background-image: url(../img/conditioner.jpg);
  }
  &--gutarnya::after {
    background-image: url(../img/glass.jpg);
  }
  &--tennis::after {
    background-image: url(../img/tennis.jpg);
  }
  &--poderevy::after {
    background-image: url(../img/tree.jpg);
  }
  &--bike::after {
    background-image: url(../img/bike.jpg);
  }
  &--roof::after {
    background-image: url(../img/roof.jpg);
  }
  &--electro::after {
    background-image: url(../img/electro.jpg);
  }
  &--tours::after {
    background-image: url(../img/tours.jpg);
  }
  &--neostyle::after {
    background-image: url(../img/barbershop.jpg);
  }

  &__heading {
    margin-top: auto;
    z-index: 7;
    padding: 0 1.5rem 1rem;
    font-size: 1.8rem;
    font-weight: 600;
  }
  &__type {
    z-index: 7;
    padding: 0 1.5rem 1.8rem;
    font-size: 1.4rem;
    font-style: italic;
  }
  &__description {
    z-index: 7;
    padding: 0 1.5rem 1.75rem;
    font-size: 1.4rem;
  }
}