.footer {

  &__section {
    @include to(576px) {
      height: auto !important;
    }
  }

  @include lg(padding-right, 4rem);
  background-color: $color-black;
  @include to(850px) {
    height: 40vh;
  }
  @include to(1050px) {
    height: 35vh;
  }
  @include to(576px) {
    display: flex;
    flex-direction: column;
    height: auto;
    padding: 2.5rem 2rem;
  }
  height: 30vh;
  padding: 5rem 5.7rem;
  box-sizing: border-box;
  @include  from(1075px) {
    display: flex;
    justify-content: space-between;
  }

  @include from-to(576px, 1075px) {
    display: grid;
    grid-template-columns: auto auto 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 1rem 2rem;
    @include to(598px) {
      gap: 0 1rem;
    }
  }

  &__logo {

    display: flex;
    margin-bottom: 2rem;
    @include from-to(576px, 680px) {
      flex-direction: column;
    }
  }
  
  &__info {
    flex-shrink: 0;
    @include to(1075px) {
      grid-row: 1 / 3;
    }

    @include to(576px) {
      order: 4;
    }
  }

  &__logo-img {
    width: 7em;
    height: 100%;
  }

  &__logo-text {
    font-size: 1.2rem;
    margin-left: 1.5rem;
    position: relative;
    top: -2px;
    @include from-to(576px, 680px) {
      margin-left: 0;
      top: 0;
      margin-top: .5rem;
    }
    @include to(576px) {
      font-size: 1.3rem;
    }
  }

  &__docs {
    display: flex;
    margin-bottom: 2rem;
    @include from-to(576px, 680px) {
      flex-direction: column;
    }
  }

  &__doc {
    font-size: 1.1rem;
    text-decoration: underline;
    &:first-child {
      margin-right: 2rem;
      @include from-to(576px, 680px) {
        margin-right: 0;
        margin-bottom: 1rem;
      }

      @include to(576px) {
        margin-right: 5rem;
      }
    }
  }

  &__politics {
    &:link,
    &:visited {
      display: block;
      font-size: 1.1rem;
      text-decoration: underline;
      margin-bottom: 1.5rem;
      @include from-to(576px, 680px) {
        margin-bottom: 1rem;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__heading {
    @include sm(font-size, 2rem);
    font-size: 1.6rem;
    font-weight: 600;
    margin-bottom: 2rem;
  }

  &__social-img {
    width: 2rem;
    &--vk {
      height: 1.25rem;
    }
  }

  &__phone {
    @include sm(font-size, 1.4rem);
    font-size: 1.3rem;
    text-decoration: underline;
    margin-bottom: 1.2rem;
  }

  &__email {
    @include sm(font-size, 1.4rem);
    font-size: 1.3rem;
  }

  &__social-text {
    @include sm(font-size, 1.4rem);
    font-size: 1.3rem;
    margin-left: 1.5rem;
  }

  &__social {
    display: flex;
    align-items: center;

    &--first {
      margin-bottom: 1.2rem;
    }
  }

  &__contacts {
    flex-shrink: 0;
    @include to(576px) {
      margin-bottom: 4rem;
    }
    @include to(1075px) {
      &:first-child {
        grid-row: 1 / 2;
      }
      &:last-child {
        grid-row: 2 / 3;
      }
    }
  }

  &__feedback {
    flex-shrink: 5;
    @include to(576px) {
      order: 3;
    }
    @include to(1075px) {
      grid-row: 1 / 3;
      grid-column: 3 / 4;
    }
  }

  &__label {
    font-size: 1.3rem;
    white-space: nowrap;
    @include sm(font-size, 1.4rem);
    @include to(1200px) {
      font-size: 1.2rem;
    }
  }

  &__icon {
    display: inline-block;
    width: 1.875em;
    height: 1.05em;
    border: 1px solid $color-white;
    border-radius: 2em;
    position: relative;
    top: .25em;
    margin-right: 0.75em;
    transition: all .3s cubic-bezier(0.6, -0.28, 0.735, 0.045);

    &::before {
      content: "";
      display: block;
      background-color: $color-white;
      width: 0.75em;
      height: 0.75em;
      border-radius: 100%;
      position: absolute;
      top: .17em;
      left: .1875em;
      transition: all .3s cubic-bezier(0.6, -0.28, 0.735, 0.045);
    }
  }

  input[type=radio] {
    display: none;
  }

  input[type=radio]:checked + &__label &__icon {
    border-color: $color-blue-1;
  }

  input[type=radio]:checked + &__label &__icon::before {
    background-color: $color-blue-1;
    left: 0.9375em;
  }

  &__text {
    @include sm(font-size, 1.3rem);
    font-size: 1.2rem;
  }

  &__options {
    @include to(850px) {
      margin-top: 1.5rem;
      margin-bottom: 1rem;
      flex-direction: column-reverse;
    }
    @include to(576px) {
      margin-bottom: 2.2rem;
    }
    margin: 3rem 0 2rem;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include to(1200px) {
      justify-content: flex-start;
    }
  }

  &__option {
    margin-right: 4rem;
    @include to(1200px) {
      width: 50%;
      margin-right: 0;
      &:last-child {
        margin-top: 1rem;
      }
    }

    @include to(850px) {
      width: 100%;
      margin-bottom: 1rem;
      &:first-child {
        margin-bottom: 0;
      }
    }

    @include to(576px) {
      margin-bottom: 1.75rem;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__btn {
    @include to(850px) {
      width: 100%;
      margin-top: 1rem;
    }
    width: 47%;
    cursor: pointer;
    padding: .8rem 0;
    text-align: center;
    transition: all .3s ease;
    background-color: $color-btn-1;
    &:hover {
      box-shadow:0 0 20px rgba($color-btn-1,.35);
    }
  }

  &__btn-text {
    font-size: 1.2em;
    font-weight: 600;
  }

  &__input-phone {
    width: 47%;
    @include to(850px) {
      width: 100%;
    }
    @include to(576px) {
      border: 1px solid $color-white;
      padding: 1rem 0rem;
      text-align: center;
      font-size: 1.4rem;
    }
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $color-white;
    color: $color-white;
    font-size: 1.3rem;
    padding-bottom: .6rem;
  }

  &__form-send {
    display: flex;
    justify-content: space-between;
    @include to(850px) {
      justify-content: flex-start;
      flex-wrap: wrap;
    }
    @include to(576px) {
      padding-bottom: 3.4rem;
      margin-bottom: 3.4rem;
      border-bottom: 1px solid $color-white;
    }
  }
}
