.logo {
  @include sm(top, 2rem);
  @include sm(left, 2rem);
  display: block;
  position: fixed;
  top: 5rem;
  left: 5.7rem;
  z-index: 250;

  &__image {
    @include sm(width, 6rem);
    width: 8rem;
  }

  &--left {
    @include sm(left, 2rem);
    position: absolute;
    left: 16%;
    top: 5rem;
  }

  &--portfolio {
    @include md(position, absolute);
    @include to(800px) {
      left: 12rem;
    }
    @include to(690px) {
      left: 8rem;
    }
  }
}