.slide-form {
  &::before {
    background-image: url(../img/form-bg.jpg);
  }

  &__content {
    max-width: 46.5%;
    overflow: hidden;
    @include sm(width, 100%);
    @include sm(margin, auto 2rem 1rem 2rem);
    @include sm(padding, 0);
    @include from-to-h(550px, 600px) {
      margin: auto 2rem 2rem 2rem;
    }
    @include to-h(550px) {
      margin: 6.5rem 2rem 0 2rem;
    }

    @include to(1300px) {
      max-width: 70%;
    }
    @include to(862px) {
      max-width: 100%;
      padding-right: 3rem;
    }
  }

  &__forms-section {
    overflow: hidden;
  }

  &__control-btn {
    @include sm(font-size, 1.4rem);
    @include to(345px) {
      font-size: 1.2rem;
    }
    @include to(380px) {
      font-size: 1.3rem;
    }
    display: inline-block;
    font-size: 1.8rem;
    font-weight: 600;
    text-transform: uppercase;
    padding: 0 .3rem .7rem .3rem;
    cursor: pointer;
    border-bottom: 3px solid transparent;
    transition: all .4s;

    &:not(:last-child) {
      margin-right: 11rem;
      @include sm(margin-right, 1.5rem);
      @include to(748px) {
        margin-right: 6rem;
      }
      @include to(632px) {
        margin-right: 5rem;
      }
    }

    &--active,
    &:hover {
      border-bottom: 3px solid $color-white;
    }
  }

  &__checkbox {
    font-size: 1.6rem;
    @include to-h(540px) {
      font-size: 1.5rem;
    }
  }

  &__settings {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 2.2rem;
    @include to(780px) {
      grid-template-columns: 1fr;
      row-gap: 2rem;
    }
    @include sm(row-gap, 1.6rem);
    @include to(345px) {
      row-gap: 1.2rem;
    }
  }

  &__icon {
    display: inline-block;
    width: 1.875em;
    height: 1em;
    border: 1px solid $color-white;
    border-radius: 2em;
    position: relative;
    top: .25em;
    margin-right: 0.75em;
    transition: all .3s cubic-bezier(0.6, -0.28, 0.735, 0.045);

    &::before {
      content: "";
      display: block;
      background-color: $color-white;
      width: 0.75em;
      height: 0.75em;
      border-radius: 100%;
      position: absolute;
      top: .125em;
      left: .1875em;
      transition: all .3s cubic-bezier(0.6, -0.28, 0.735, 0.045);
    }
  }

  input[type=checkbox] {
    display: none;
  }

  input[type=checkbox]:checked + &__checkbox &__icon {
    border-color: $color-blue-1;
  }

  input[type=checkbox]:checked + &__checkbox &__icon::before {
    background-color: $color-blue-1;
    left: 0.9375em;
  }

  &__text {
    margin-bottom: 4rem;
    @include sm(font-size, 1.4rem);
    @include sm(margin-bottom, 2.5rem);
    @include to-h(540px) {
      font-size: 1.3rem;
      margin-bottom: 1.5rem;
    }
  }

  &__controls {
    margin: 0 0 2rem 3rem;
    @include sm(margin, 0 0 2.5rem 0);
    @include to(380px) {
      margin: 0 0 1rem 0;
    };
    @include to(632px) {
      margin-left: 0 !important;
    }
  }

  &__submit {
    @include sm(margin-top, 1.5rem);
    background-color: $color-btn-1;
    display: inline-block;
    padding: 1.3rem 2.7rem;
    font-size: 1.3rem;
    font-weight: 600;
    text-transform: uppercase;
    cursor: pointer;
    margin-top: 4.5rem;
    transition: all .3s ease;
    &:hover {
      box-shadow:0 0 20px rgba($color-btn-2,.35);
    }

  }

  &__price {
    font-size: 2rem;
    margin-right: 4rem;
    display: inline-block;
    font-style: italic;
    margin-top: 5rem;
    @include sm(margin-top, 2.5rem);
    @include to(345px) {
      margin-top: 1.5rem;
    };
  }

  &__time {
    font-size: 1.6rem;
    display: inline-block;
  }

  &__chipper {
    font-size: 1.2rem;
    margin-top: 2rem;
    @include sm(margin-top, 1.5rem);
    @include to-h(540px) {
      margin-top: 0.5rem;
    }
  }

}