.view-design {
  @include sm(padding, 8.5rem 2rem 5%);
  height: auto;
  padding: 12.5rem 16% 3%;
  width: 100vw;
  box-sizing: border-box;
  position: relative;

  &__img {
    display: block;
    width: 100%;
    box-sizing: border-box;
    box-shadow: 0 5px 10px 3px rgba(0, 0, 0, 0.3);
  }

  &::before {
    content: "";
    position: absolute;
    top: 0; left: 0;
    width: 100vw;
    height: 100vh;
    
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    z-index: -1;
    filter: brightness(56%);
  }
  &--sochi::before {
    background-image: url(../img/sochi.jpg);
  }
  &--plumbing::before {
    background-image: url(../img/plumbing.jpg);
  }
  &--mount::before {
    background-image: url(../img/explore.jpg);
  }
  &--sveter::before {
    background-image: url(../img/conditioner.jpg);
  }
  &--gutarnya::before {
    background-image: url(../img/glass.jpg);
  }
  &--tennis::before {
    background-image: url(../img/tennis.jpg);
  }
  &--poderevy::before {
    background-image: url(../img/tree.jpg);
  }
  &--bike::before {
    background-image: url(../img/bike.jpg);
  }
  &--roof::before {
    background-image: url(../img/roof.jpg);
  }
  &--electro::before {
    background-image: url(../img/electro.jpg);
  }
  &--tours::before {
    background-image: url(../img/tours.jpg);
  }
  &--neostyle::before {
    background-image: url(../img/barbershop.jpg);
  }
  &::after {
    content: "";
    position: absolute;
    top: 0; left: 0;
    width: 100vw;
    height: 100%;
    background-image: linear-gradient(0deg, rgba(0,0,0,1) calc(100% - 99vh), rgba(255,255,255,0) 100%);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    z-index: -1;
    filter: brightness(56%);
  }

  &__heading {
    line-height: 1.2;
  }

  &__goals {
    @include md(flex-direction, column);
    @include md(margin-bottom, 5rem);
    @include sm(margin-top, 6rem);
    margin-top: 8rem;
    display: flex;
    margin-bottom: 4rem;
  }

  &__goal {
    @include md(width, 100%);
    width: 40%;
    flex-shrink: 0;

    &:not(:last-child) {
      @include md(margin-bottom, 4rem);
      @include md(margin-right, 0);
      margin-right: 4rem;
    }
  }

  &__goal-heading {
    font-size: 2.4rem;
    font-style: italic;
    margin-bottom: 1.8rem;
  }

  &__price {
    font-size: 2.8rem;
    font-style: italic;
    margin-bottom: 5rem;

    &--bold {
      font-weight: 600;
    }
  }

  &__input {
    width: 16rem;
    box-sizing: border-box;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $color-white;
    padding: 1rem 0;
    font-size: 1.4rem;
    color: $color-white;
    text-align: center;
  }

  &__input::placeholder {
    color: $color-white;
  }

  &__feedback-heading {
    text-transform: uppercase;
    font-size: 1.6rem;
    letter-spacing: .5px;
    font-weight: 600;
    margin-bottom: 1.5rem;
  }

  &__text {
    &--bold {
      font-weight: 600;
    }
  }

  &__feedback {
    max-width: 66rem;
    margin-top: 6rem;
  }

  &__feedback-phone {
    margin-top: 2rem;
    display: flex;
    @include sm(flex-direction, column);
  }

  &__submit {
    @include sm(margin-left, 0);
    @include sm(margin-top, 2rem);
    @include sm(width, 10rem);
    background-color: $color-btn-1;
    padding: 1rem 4.5rem;
    margin-left: 3rem;
    font-size: 1.3rem;
    font-weight: 600;
    text-transform: uppercase;
    cursor: pointer;
    transition: all .3s ease;
    &:hover {
      box-shadow:0 0 20px rgba($color-btn-2,.35);
    }
  }
}