.slide-together {
  &::before {
    background-image: url(../img/together-bg.jpg);
  }

  &__content {
    max-width: 88.3rem;
    position: static;
  }

  &__text {
    @include sm(padding-right, 0);
    padding-right: 2rem;
    &:last-child {
      margin-top: 2rem;
      padding-bottom: 2rem;
    }
  }
}