.portfolio {
  margin-right: 2rem;

  &__wrapper {
    @include from(768px) {
      display: grid;
      height: 100%;
      grid-template-rows: 23vh 23vh;
      gap: 1.2rem;
      grid-template-columns: 1fr 1fr 1fr 5.5rem;
    }
    @include to(576px) {
      height: 62.5vh;
      margin-left: 2rem;
    }

    @include from-to(576px, 768px) {
      display: grid;
      height: 100%;
      grid-template-rows: 23vh 23vh;
      gap: 1.2rem;
      grid-template-columns: 1fr 1fr 5.5rem;
    }
  }

  &__slide {
    width: 82.5%;
    box-sizing: border-box;

    & .portfolio__example:first-child {
      margin-bottom: 15px;
    }

    &:nth-child(3) {
      width: calc(82.5% - 4rem);
    }

    &:nth-child(4) {
      width: calc(17.5% - 2rem);
      margin-right: 4rem;
    }
  }

  &__example {
    @include sm(width, 100%);
    @include sm(height, calc(( 100% - 15px ) / 2));
    background-color: $color-grey;
    border-bottom: 3px solid $color-grey;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    overflow: hidden;
    height: 100%;
    box-sizing: border-box;

    &::before{
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 0;
      background-image: linear-gradient(180deg, transparent 25%, $color-black 100%);
      transition: all 0.4s ease-in-out;
      z-index: 6;
    }

    &::after{
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: center center;
      z-index: 5;
    }

    &--1::after {
      background-image: url(../img/sochi.jpg);
    }
    &--2::after {
      background-image: url(../img/plumbing.jpg);
    }
    &--3::after {
      background-image: url(../img/explore.jpg);
    }
    &--4::after {
      background-image: url(../img/conditioner.jpg);
    }
    &--5::after {
      background-image: url(../img/glass.jpg);
    }
    &--6::after {
      background-image: url(../img/tennis.jpg);
    }
    
    &:hover {
      &::before {
        height: 100%;
      }

      .portfolio__text {
        transform: translateY(0);
      }
    }

    &--5, &--6 {
      @include from-to(576px, 768px) {
        display: none;
      }
    }
  }

  &__text {
    z-index: 250;
    position: absolute;
    bottom: 25px;
    transform: translateY(calc(100% + 25px));
    transition: all 0.4s ease-in-out;
    font-size: 1.6rem;
    text-align: center;
    padding: 0 .5rem;
  }

  &__btn {
    @include md(height, 100%);
    @include md(display, block);
    @include from(576px) {
      grid-row: 1 / 3;
      grid-column: 3 / 4;
    }
    @include from(769px) {
      grid-row: 1 / 3;
      grid-column: 4 / 5;
    }
    position: relative;
    background-color: $color-btn-1;
    cursor: pointer;
    transition: all .3s ease;

    &:hover {
      box-shadow:0 0 20px rgba($color-btn-2,.35);
    }
  }

  &__btn-text {
    position: absolute;
    top: 50%;
    left: 50%;
    text-transform: uppercase;
    word-spacing: 150%;
    transform-origin: 0 0;
    transform: rotateZ(90deg) translateX(-50%) translateY(calc(50% - 19.6px));
    width: 14rem;
    font-weight: 600;
    white-space: nowrap;
  }
}